<template>
  <div class="bg-white sticky-top">
    <div class="container">
      <nav class="navbar px-0 navbar-expand-lg navbar-light bg-white">
        <router-link class="navbar-brand position-absolute" to="./"
        style="left: 50%; transform: translate(-50%, -50%); top: 50%;">Home</router-link>
        <!-- <a class="navbar-brand position-absolute" href="./index.html"
          style="left: 50%; transform: translate(-50%, -50%); top: 50%;">
          Navbar
        </a> -->
        <button class="navbar-toggler" type="button" data-bs-toggle="collapse"
        data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false"
        aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse bg-white custom-header-md-open" id="navbarNav">
          <ul class="navbar-nav">
            <li class="nav-item active">
              <router-link class="nav-link" to="/products">產品列表</router-link>
            </li>
          </ul>
        </div>
        <div class="d-flex">
          <div class="position-relative">
          <router-link to="/cart">
          <i class="btn btn-sm bi bi-bag"></i>
          <span class="position-absolute top-0 start-100
          translate-middle badge rounded-pill bg-danger">
            {{ cartData?.length }}
            <span class="visually-hidden">unread messages</span>
          </span>
          </router-link>
          </div>
        </div>
      </nav>
    </div>
  </div>
</template>

<script>
import emitter from '@/utils/emitter';

export default {
  data() {
    return {
      cartData: [],
    };
  },
  methods: {
    getCart() {
      // console.log(this.id);
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
      this.$http.get(url)
        .then((res) => {
          console.log('getcart', res);
          this.cartData = res.data.data.carts;
        })
        .catch((err) => {
          console.dir(err);
        });
    },
  },
  mounted() {
    this.getCart();
    emitter.on('get-cart', () => {
      this.getCart();
    });
  },
};
</script>
